@import 'Config';
@import 'Utilities';
@import 'Item_grid';

// General Styles
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Dosis', sans-serif;
  line-height: 1.6;
  background: #fff;
}

a {
  text-decoration: none;
  color: $dark-color;
}

ul {
  list-style: none;
}

h2,
h3,
h4 {
  text-transform: uppercase;
}

img {
  width: 100%;
}
.navbar-expand {
  position: fixed;
  height: 50px  ;
  width: 100%;
  background: #18394E;


}

// Logo
#logo {
  width: 70px;
  height: 70px;
  color: #fff;
  text-transform: uppercase;
}

// Main Nav
#main-nav {
  width: 100%;
  position: fixed;
  z-index: 555;
  display: flex;
  justify-content: space-between;
  padding-top: .5rem;
  background: #18394E;




  ul {
    display: flex;
    margin-left: auto;




  }

  li {
    padding: .2rem 1.5rem;

  }

  a {
    line-height: 1rem;
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    border-bottom: 3px transparent solid;
    //padding-bottom: 0.5rem;
    transition: border-color 0.5s;

    &:hover {
      border-color: $medium-color;
    }

    &.current {
      border-color: $main-color;
    }
  }
}

// Header
#header {
  // Header Home
  &-home {
    position: absolute;
    z-index:-1;
    width: 100%;
    top: 0;
    left: 0;
    background: $bg-image no-repeat center right / cover;
    height: 100vh;
    color: #fff;


    // Header Content
    .header-content {
      text-align: center;
      padding-top: 20%;

      h1 {
        font-size: 4rem;
        line-height: 1.2;
      }
      span {
        font-size: 45px;
      }
    }
  }

  // Header Inner
  &-inner {
    background: $bg-image no-repeat 20% 30% / cover;
    height: 5.5rem;
    border-bottom: 3px solid $main-color;
  }
}

