acity:0;text-align:center;z-index:1;color:#fff;transform:translateY(-20%);transition:opacity 500ms cubic-bezier(0.2, 1, 0.3, 1),transform 500ms cubic-bezier(0.2, 1, 0.3, 1);transition-delay:300ms}.item-text-wrap{position:absolute;width:100%;top:50%;transform:translateY(-50%)}.item-text-title{font-size:2rem;padding:0 1rem;margin:5px 0 0 0}.item-text-category{text-transform:uppercase;font-size:1.2rem;opacity:0.7;margin:0}*{margin:0;padding:0}body{font-family:'Dosis', sans-serif;line-height:1.6;background:#fff}a{text-decoration:none;color:#333}ul{list-style:none}h2,h3,h4{text-transform:uppercase}img{width:100%}.navbar-expand{position:fixed;height:50px;width:100%;background:#18394E}#logo{width:70px;height:70px;color:#fff;text-transform:uppercase}#main-nav{width:100%;position:fixed;z-index:555;display:flex;justify-content:space-between;padding-top:.5rem;background:#18394E}#main-nav ul{display:flex;margin-left:auto}#main-nav li{padding:.2rem 1.5rem}#main-nav a{line-height:1rem;text-decoration:none;color:#fff;text-transform:uppercase;border-bottom:3px transparent solid;transition:border-color 0.5s}#main-nav a:hover{border-color:#ccc}#main-nav a.current{border-color:#ffbc00}#header-home{position:absolute;z-index:-1;width:100%;top:0;left:0;background:url("../Img/bg2.jpg") no-repeat center right/cover;height:100vh;color:#fff}#header-home .header-content{text-align:center;padding-top:20%}#header-home .header-content h1{font-size:4rem;line-height:1.2}#header-home .header-content span{font-size:45px}#header-inner{background:url("../Img/bg2.jpg") no-repeat 20% 30%/cover;height:5.5rem;border-bottom:3px solid #ffbc00}#about-a .about-info{display:grid;grid-template-areas:'bioimage bio bio' 'aw1 aw2 aw3';grid-gap:1.2rem}#about-a .bio-image{grid-area:bioimage}#about-a .bio{grid-area:bio;border-left:3px solid #ffbc00;padding:0.8rem}#about-b .progress{overflow:hidden;height:10px;background:#ccc;border-radius:5px;margin-bottom:0.3rem}#about-b .progress div{height:100%;color:#fff;text-al